  #App-side-bar-container {
    top: 0;
    left: 0;
    position: fixed;
    z-index: 1;
    height: 100%;
    width: 25%;
    min-width: 150px;

    z-index: 1;

    float: left;
  }

  .Side-bar {
    position: relative;
    background-color: #93CFE1/*#738adb*/;
    width: 100%;
    height: 100%;
    max-width: 100%;
    overflow-x: hidden;
    overflow-y: hidden;
  }
  
  #Side-bar-content {
    margin: 5% 5% 0 5%;
    border: none;
    overflow-x: hidden;
    overflow-y: hidden;
  }
 
  .Side-bar-cards-container {
    min-width: 100px;
  }

  #Profile-picture {
    width: auto;
    height: auto;
    max-width: 50%;
    max-height: 50%;
  }

  #Side-bar-nav {
    display: block;
    width: 80%;
    margin: 2.5%;
  }

  #Side-bar-contacts {
    margin: 2.5%;
  }
  
  .Side-bar-card-link {
    display: block;
    text-decoration: none;
    width: 100%;
    color: black;
    margin: -1em auto -1em auto;
  }
  
  .Side-bar-nav-link {
    margin: -0.1em auto auto auto;
    min-width: 72px;
  }
  
  
  .Side-bar-nav-submit {
    background-color: #93CFE1/*#738adb*/;
    width: 100%;
    /*padding: 5%;*/
  
    /*Border*/
    border-width: 1px;
    border-style: solid;
    border-image: linear-gradient(to right, #93CFE1, rgba(0, 0, 0, 0.8),#93CFE1) 1 10%;/*linear-gradient(to right, #738adb, rgba(0, 0, 0, 0.8),#738adb) 1 10%;*/
    border-left: none;
    border-right: none;

    /*Text*/
    font-size: 14pt;
  }
  
  .Side-bar-card-line {
    display: block;
    width: auto;
  }
  
  .Side-bar-card-icon {
    font-size: 18pt;
    margin: -5px 0px -5px 0px;
  }
  
  .Side-bar-card-text {
    font-size: 10pt;
    display: inline-block;
    margin: auto auto auto auto;
    font-weight: 600;
    text-align: center;
  }
  
  /*Mobile version will be contained here*/
  @media screen and (max-width: 800px){
    .Side-bar-card-text {
      font-size: 6pt;
      display: inline-block;
      margin: auto auto auto auto;
      word-wrap: break-word;
    }
  
    .Side-bar-card-icon {
      font-size: 12pt;
      margin: -5px 0px -5px 0px;
    }
  
    .email-text {
      font-size: 6pt;
    }

    .Side-bar-nav-submit {
        font-size: 10pt;
    }
  }
